.main-header {
  background-color: var(--secondary-color);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 3rem), 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 3rem), 0 100%);
  text-align: center;
  padding-bottom: 64px;
  position: relative;
  overflow: hidden;

  h2 {
    margin: 0;
  }

  p {
    margin-top: 8px;
  }

  a {
    margin-left: 5px;
    margin-right: 5px;
  }
}
